import React, { useEffect } from "react";
import { history } from "router";
import { useAuth } from "store/auth/action";

export default function App({ children }: { children: React.ReactNode }) {
  const { fetchWxConfig } = useAuth();

  useEffect(() => {
    fetchWxConfig({ config_url: window.location.href.split("#")[0] });

    history.listen(() => {
      fetchWxConfig({ config_url: window.location.href.split("#")[0] });
    });
  }, [fetchWxConfig]);

  return <div id="App">{children}</div>;
}
