import { DatePicker, Picker } from "antd-mobile";
import Loading from "components/loading";
import * as echarts from "echarts";
import { EChartsOption } from "echarts";
import moment from "moment";
import { Fragment, useEffect } from "react";
import { useFetchShopReport, useShopReport } from "store/shop-report/action";
import { useDocumentTitle } from "utils/hook";
import "./index.scss";

const timeOptions = [
  [
    {
      label: "日报",
      value: 0,
    },
    {
      label: "月报",
      value: 1,
    },
    {
      label: "年报",
      value: 2,
    },
    {
      label: "自定义范围",
      value: 3,
    },
  ],
];

export const ShopReportPage = () => {
  const {
    shopReportInput,
    shopReportPage,
    shopReportOutput: { current_store, store_options },
    fetchShopReport,
    fetchShopReportPending,
    fetchShopReportError,
  } = useFetchShopReport();
  const {
    datePlusOneDay,
    dateMinusOneDay,
    changeCustomStartPicker,
    changeCustomEndPicker,
    changeTimeOptionsPicker,
    changeShopOptionsPicker,
    setShopReportInput,
  } = useShopReport();

  const onHandleDatePickerConfirm = (date) => {
    const time_str =
      moment(date).format("YYYY-MM-DD") + "/" + moment().format("YYYY-MM-DD");
    changeCustomStartPicker();
    setShopReportInput({
      time_str: shopReportInput.dimension === 3 ? time_str : date,
    });
  };

  const onHandleDatePickerEndConfirm = (date) => {
    const arr = shopReportInput.time_str.split("/");
    const time_str = arr[0] + "/" + moment(date).format("YYYY-MM-DD");
    changeCustomEndPicker();
    setShopReportInput({ time_str });
  };

  const onHandleTimeOptionsConfirm = (value) => {
    let time_str = "";
    if (value[0] === 3) {
      time_str =
        moment().format("YYYY-MM-DD") + "/" + moment().format("YYYY-MM-DD");
    } else {
      time_str = moment().format("YYYY-MM-DD");
    }

    changeTimeOptionsPicker();
    setShopReportInput({ dimension: value[0], time_str });
  };

  const onHandleShopOptionsConfirm = (value) => {
    changeShopOptionsPicker();
    setShopReportInput({ store_id: value[0] });
  };

  useEffect(() => {
    fetchShopReport();
  }, [fetchShopReport]);

  useEffect(() => {
    const echartDom = document.getElementById("main");
    if (echartDom && shopReportPage.chartData) {
      const myChart = echarts.init(echartDom);

      var option: EChartsOption = {
        title: {
          textStyle: {
            fontSize: 14,
          },
          text: shopReportPage.chartData.title,
        },
        tooltip: {},
        legend: {
          data: [shopReportPage.chartData.unit],
          top: 20,
        },
        xAxis: {
          data: shopReportPage.chartData.data.map((item) => item.title),
        },
        yAxis: {},
        series: [
          {
            name: shopReportPage.chartData.unit,
            type: "bar",
            data: shopReportPage.chartData.data.map((item) => item.value),
            label: {
              show: true,
              position: "top",
            },
          },
        ],
      };

      myChart.setOption(option);
    }
  }, [shopReportPage.chartData]);

  useDocumentTitle("门店报表系统");

  return (
    <div className="shop-report-page-container">
      {fetchShopReportPending ? (
        <Loading minHeight="100vh" />
      ) : fetchShopReportError ? null : (
        <Fragment>
          <div className="business-analysis-header-fixed">
            <h3 className="shopname">{current_store.name}</h3>
            <div className="business-analysis-time">
              <div
                className={`prev-btn ${
                  shopReportInput.dimension === 3 ? "disabled-btn" : ""
                }`}
                onClick={dateMinusOneDay}
              >
                <i className="ladyn ladyn-arrow-left-filling"></i>
              </div>

              <div className="time-content">
                {shopReportInput.dimension === 3 ? (
                  <div className="date-content">
                    <span onClick={changeCustomStartPicker}>
                      {shopReportInput.time_str.split("/")[0]}
                    </span>
                    <span> 至 </span>
                    <span onClick={changeCustomEndPicker}>
                      {shopReportInput.time_str.split("/")[1]}
                    </span>
                  </div>
                ) : (
                  <div
                    className="date-content"
                    onClick={changeCustomStartPicker}
                  >
                    {shopReportInput.time_str}
                  </div>
                )}

                <div className="toggle-content">
                  <div
                    className="date-mode mode"
                    onClick={changeTimeOptionsPicker}
                  >
                    <span className="text">
                      {shopReportPage.timeOptionText}
                    </span>
                    <i className="ladyn ladyn-qiehuan"></i>
                  </div>
                  {store_options.length > 0 ? (
                    <div
                      className="shop-mode mode"
                      onClick={changeShopOptionsPicker}
                    >
                      <span className="text">门店</span>
                      <i className="ladyn ladyn-qiehuan"></i>
                    </div>
                  ) : null}
                </div>
              </div>

              <div
                className={`next-btn ${
                  shopReportInput.dimension === 3 ||
                  shopReportPage.disabledNextDate
                    ? "disabled-btn"
                    : ""
                }`}
                onClick={datePlusOneDay}
              >
                <i className="ladyn ladyn-arrow-right-filling"></i>
              </div>
            </div>
          </div>

          <div
            id="main"
            style={{ width: "calc(100% - 20px)", height: "400px" }}
          ></div>

          <div className="list">
            {shopReportPage.dataList.map((item, index) => (
              <div className="content-wrap" key={index}>
                <h3 className="title">{item.title}</h3>
                {index === 0 ? (
                  <div className="content-1">
                    {item.data.map((item, index) => (
                      <div className="item" key={index}>
                        <div className="sub-item">
                          <div className="subtitle">{item.title}</div>
                          <div className="text">{item.value}</div>
                        </div>
                      </div>
                    ))}
                  </div>
                ) : null}
                {index === 1 ? (
                  <div className="content-2">
                    {item.data.map((item, index) => (
                      <div
                        className={`item ${
                          item.data.length === 1 ? "one" : "two"
                        }`}
                        key={index}
                      >
                        <div className="subtitle">
                          <span className="text">{item.title}</span>
                          <span className="unit">单位：{item.unit}</span>
                        </div>
                        <div className="content">
                          {item.data.map((data, index) => (
                            <div className="sub-item" key={index}>
                              <div className="sub-item-content">
                                <div className="sub-item-title">
                                  {data.title}
                                </div>
                                <div className="sub-item-text">
                                  {data.value}
                                </div>
                              </div>
                            </div>
                          ))}
                        </div>
                      </div>
                    ))}
                  </div>
                ) : null}
              </div>
            ))}
          </div>
        </Fragment>
      )}

      <DatePicker
        title="选择时间"
        mode={shopReportPage.datePickerMode}
        value={shopReportPage.datePickerDefaultDate}
        maxDate={shopReportPage.datePickerDefaultDate}
        visible={shopReportPage.datePickerVisible}
        onDismiss={changeCustomStartPicker}
        onOk={onHandleDatePickerConfirm}
      />

      <DatePicker
        title="选择结束时间"
        mode="date"
        value={shopReportPage.datePickerEndDefaultDate}
        maxDate={shopReportPage.datePickerEndMaxDate}
        visible={shopReportPage.datePickerEndVisible}
        onDismiss={changeCustomEndPicker}
        onOk={onHandleDatePickerEndConfirm}
      />

      <Picker
        title="请选择类型"
        data={timeOptions}
        value={[shopReportInput.dimension]}
        cascade={false}
        visible={shopReportPage.timeOptionsPickerVisible}
        onDismiss={changeTimeOptionsPicker}
        onOk={onHandleTimeOptionsConfirm}
      />

      <Picker
        title="请选择门店"
        data={[shopReportPage.shopList]}
        value={[current_store.id]}
        cascade={false}
        visible={shopReportPage.shopOptionsPickerVisible}
        onDismiss={changeShopOptionsPicker}
        onOk={onHandleShopOptionsConfirm}
      />
    </div>
  );
};

export const shopReportRoute = {
  path: "",
  childRoutes: [{ path: "shop-report", component: ShopReportPage }],
};
