import { Grid } from "antd-mobile";
import { HomeGridItem } from "model/home";
import { useHistory } from "react-router-dom";
import { useHomePageState } from "store/home";

export const HomePage = () => {
  const history = useHistory();
  const { homeGridList } = useHomePageState();
  const onHandleClick = (item: HomeGridItem) => {
    history.push(item.path);
  };

  return (
    <div className="home-page-container">
      <Grid hasLine columnNum={3} data={homeGridList} onClick={onHandleClick} />
    </div>
  );
};

export const homeRoute = {
  path: "",
  childRoutes: [{ path: "home", component: HomePage, isIndex: true }],
};
