import { GroupReportPageState } from "model/group-report";
import moment from "moment";
import { GroupReportAction } from "./action";
import {
  FETCH_GROUP_REPORT_BEGIN,
  FETCH_GROUP_REPORT_FAILURE,
  FETCH_GROUP_REPORT_SUCCESS,
  GROUP_REPORT_CHANGE_CUSTOM_END_PICKER,
  GROUP_REPORT_CHANGE_CUSTOM_START_PICKER,
  GROUP_REPORT_CHANGE_TIME_OPTIONS_PICKER,
  GROUP_REPORT_DAY_MINUS_ONE,
  GROUP_REPORT_DAY_PLUS_ONE,
  SET_GROUP_REPORT_PARAMS,
} from "./constant";

export function groupReportReducer(
  state: GroupReportPageState,
  action: GroupReportAction
) {
  switch (action.type) {
    case FETCH_GROUP_REPORT_BEGIN:
      return {
        ...state,
        fetchGroupReportPending: true,
        fetchGroupReportError: null,
      };

    case FETCH_GROUP_REPORT_SUCCESS:
      let disabledNextDate = false;
      if (state.groupReportInput.dimension === 0) {
        disabledNextDate = moment(state.groupReportInput.time_str)
          .add(1, "days")
          .isAfter(moment(), "days");
      } else if (state.groupReportInput.dimension === 1) {
        disabledNextDate = moment(state.groupReportInput.time_str)
          .add(1, "months")
          .isAfter(moment(), "months");
      } else if (state.groupReportInput.dimension === 2) {
        disabledNextDate = moment(state.groupReportInput.time_str)
          .add(1, "years")
          .isAfter(moment(), "years");
      }

      return {
        ...state,
        groupReportOutput: {
          ...action.data,
        },
        groupReportPage: {
          ...state.groupReportPage,
          disabledNextDate,
        },
        fetchGroupReportPending: false,
        fetchGroupReportError: null,
      };

    case FETCH_GROUP_REPORT_FAILURE:
      return {
        ...state,
        fetchGroupReportPending: false,
        fetchGroupReportError: action.data.error,
      };

    case GROUP_REPORT_DAY_PLUS_ONE: {
      let time_str = "";
      if (state.groupReportInput.dimension === 0) {
        time_str = moment(state.groupReportInput.time_str)
          .add(1, "days")
          .format("YYYY-MM-DD");
      } else if (state.groupReportInput.dimension === 1) {
        time_str = moment(state.groupReportInput.time_str)
          .add(1, "months")
          .format("YYYY-MM");
      } else if (state.groupReportInput.dimension === 2) {
        time_str = moment(state.groupReportInput.time_str)
          .add(1, "years")
          .format("YYYY");
      }

      return {
        ...state,
        groupReportInput: {
          ...state.groupReportInput,
          time_str,
        },
      };
    }

    case GROUP_REPORT_DAY_MINUS_ONE: {
      let time_str = "";
      if (state.groupReportInput.dimension === 0) {
        time_str = moment(state.groupReportInput.time_str)
          .subtract(1, "days")
          .format("YYYY-MM-DD");
      } else if (state.groupReportInput.dimension === 1) {
        time_str = moment(state.groupReportInput.time_str)
          .subtract(1, "months")
          .format("YYYY-MM");
      } else if (state.groupReportInput.dimension === 2) {
        time_str = moment(state.groupReportInput.time_str)
          .subtract(1, "years")
          .format("YYYY");
      }

      return {
        ...state,
        groupReportInput: {
          ...state.groupReportInput,
          time_str,
        },
      };
    }

    case GROUP_REPORT_CHANGE_TIME_OPTIONS_PICKER:
      return {
        ...state,
        groupReportPage: {
          ...state.groupReportPage,
          timeOptionsPickerVisible: action.data.visible,
        },
      };

    case GROUP_REPORT_CHANGE_CUSTOM_START_PICKER:
      return {
        ...state,
        groupReportPage: {
          ...state.groupReportPage,
          datePickerVisible: action.data.visible,
        },
      };

    case GROUP_REPORT_CHANGE_CUSTOM_END_PICKER:
      return {
        ...state,
        groupReportPage: {
          ...state.groupReportPage,
          datePickerEndVisible: action.data.visible,
        },
      };

    case SET_GROUP_REPORT_PARAMS:
      let timeOptionText = "";
      let datePickerMode:
        | "datetime"
        | "date"
        | "year"
        | "month"
        | "time"
        | undefined;
      let params: any = {};
      if (action.data.dimension === 0) {
        timeOptionText = "按日选择";
        datePickerMode = "date";
        params.time_str = moment(action.data.time_str).format("YYYY-MM-DD");
      } else if (action.data.dimension === 1) {
        timeOptionText = "按月选择";
        datePickerMode = "month";
        params.time_str = moment(action.data.time_str).format("YYYY-MM");
      } else if (action.data.dimension === 2) {
        timeOptionText = "按年选择";
        datePickerMode = "year";
        params.time_str = moment(action.data.time_str).format("YYYY");
      } else {
        timeOptionText = "自定义范围";
        datePickerMode = "date";
      }

      return {
        ...state,
        groupReportInput: {
          ...state.groupReportInput,
          ...action.data,
          ...params,
        },
        groupReportPage: {
          ...state.groupReportPage,
          timeOptionText,
          datePickerMode,
        },
      };
    default:
      return state;
  }
}
