import { AuthState } from "model/auth";
import { AuthAction } from "./action";
import {
  AUTH_FETCH_WXCONFIG_BEGIN,
  AUTH_FETCH_WXCONFIG_FAILURE,
  AUTH_FETCH_WXCONFIG_SUCCESS,
} from "./constant";

export default function authReducer(state: AuthState, action: AuthAction) {
  switch (action.type) {
    case AUTH_FETCH_WXCONFIG_BEGIN:
      return {
        ...state,
        fetchWxConfigPending: true,
        fetchWxConfigError: null,
      };

    case AUTH_FETCH_WXCONFIG_SUCCESS:
      return {
        ...state,
        wxConfig: {
          ...action.data,
        },
        fetchWxConfigPending: false,
        fetchWxConfigError: null,
      };

    case AUTH_FETCH_WXCONFIG_FAILURE:
      return {
        ...state,
        fetchWxConfigPending: false,
        fetchWxConfigError: action.data.error,
      };
    default:
      return state;
  }
}
