import { WxConfigOutput } from "model/auth";
import { ActionType } from "model/common";
import { useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import http from "utils/http";
import ApiUrl from "utils/url";
import { getWxSdkConfig } from "utils/wxsdk";
import { RootStore } from "../reducers";
import {
  AUTH_FETCH_WXCONFIG_BEGIN,
  AUTH_FETCH_WXCONFIG_FAILURE,
  AUTH_FETCH_WXCONFIG_SUCCESS,
} from "./constant";

export const fetchAuthWxConfigSuccess = (data: WxConfigOutput) => ({
  type: AUTH_FETCH_WXCONFIG_SUCCESS,
  data,
});
export const fetchAuthWxConfigError = (data: { error: any }) => ({
  type: AUTH_FETCH_WXCONFIG_FAILURE,
  data,
});
export function fetchWxConfigAsyncAction(params?: { config_url: string }) {
  return async (dispatch) => {
    dispatch({
      type: AUTH_FETCH_WXCONFIG_BEGIN,
    });
    try {
      const { data } = await http.get(ApiUrl.WxConfig, { params });
      getWxSdkConfig(data);
      dispatch(fetchAuthWxConfigSuccess(data));
    } catch (error) {
      dispatch(fetchAuthWxConfigError({ error }));
    }
  };
}

export function useAuth() {
  const dispatch = useDispatch();
  const { wxConfig, fetchWxConfigError, fetchWxConfigPending } = useSelector(
    (state: RootStore) => state.auth
  );

  const fetchWxConfig = useCallback(
    (params?: { config_url: string }) => {
      dispatch(fetchWxConfigAsyncAction(params));
    },
    [dispatch]
  );

  return {
    wxConfig,
    fetchWxConfig,
    fetchWxConfigPending,
    fetchWxConfigError,
  };
}

export type AuthAction = ActionType<typeof fetchAuthWxConfigSuccess> &
  ActionType<typeof fetchAuthWxConfigError>;
