import { DatePicker, Picker } from "antd-mobile";
import Loading from "components/loading";
import moment from "moment";
import { Fragment, useEffect } from "react";
import { useFetchGroupReport, useGroupReport } from "store/group-report/action";
import { useDocumentTitle } from "utils/hook";
import "./index.scss";

const timeOptions = [
  [
    {
      label: "按日",
      value: 0,
    },
    {
      label: "按月",
      value: 1,
    },
    {
      label: "按年",
      value: 2,
    },
    {
      label: "自定义范围",
      value: 3,
    },
  ],
];

const filterOptions = [
  {
    type: 0,
    text: "累计",
  },
  {
    type: 1,
    text: "日均",
  },
];
export const GroupReportPage = () => {
  const {
    groupReportInput,
    groupReportOutput,
    groupReportPage,
    fetchGroupReport,
    fetchGroupReportPending,
  } = useFetchGroupReport();

  const {
    datePlusOneDay,
    dateMinusOneDay,
    changeCustomStartPicker,
    changeCustomEndPicker,
    changeTimeOptionsPicker,
    setGroupReportInput,
  } = useGroupReport();

  const onHandleDatePickerConfirm = (date) => {
    const time_str =
      moment(date).format("YYYY-MM-DD") + "/" + moment().format("YYYY-MM-DD");
    changeCustomStartPicker();
    setGroupReportInput({
      time_str: groupReportInput.dimension === 3 ? time_str : date,
    });
  };

  const onHandleDatePickerEndConfirm = (date) => {
    const arr = groupReportInput.time_str.split("/");
    const time_str = arr[0] + "/" + moment(date).format("YYYY-MM-DD");
    changeCustomEndPicker();
    setGroupReportInput({ time_str });
  };

  const onHandleTimeOptionsConfirm = (value) => {
    let time_str = "";
    if (value[0] === 3) {
      time_str =
        moment().format("YYYY-MM-DD") + "/" + moment().format("YYYY-MM-DD");
    } else {
      time_str = moment().format("YYYY-MM-DD");
    }

    changeTimeOptionsPicker();
    setGroupReportInput({ dimension: value[0], time_str });
  };

  useEffect(() => {
    fetchGroupReport();
  }, [fetchGroupReport]);

  useDocumentTitle("总部报表系统");

  return (
    <div className="group-report-page-container">
      {fetchGroupReportPending ? (
        <Loading minHeight="100vh" />
      ) : (
        <Fragment>
          <div className="business-analysis-header-fixed">
            <div className="business-analysis-time">
              <div
                className={`prev-btn ${
                  groupReportInput.dimension === 3 ? "disabled-btn" : ""
                }`}
                onClick={dateMinusOneDay}
              >
                <i className="ladyn ladyn-arrow-left-filling"></i>
              </div>

              <div className="time-content">
                {groupReportInput.dimension === 3 ? (
                  <div className="date-content">
                    <span onClick={changeCustomStartPicker}>
                      {groupReportInput.time_str.split("/")[0]}
                    </span>
                    <span> 至 </span>
                    <span onClick={changeCustomEndPicker}>
                      {groupReportInput.time_str.split("/")[1]}
                    </span>
                  </div>
                ) : (
                  <div
                    className="date-content"
                    onClick={changeCustomStartPicker}
                  >
                    {groupReportInput.time_str}
                  </div>
                )}

                <div
                  className="toggle-content"
                  onClick={changeTimeOptionsPicker}
                >
                  <div className="date-mode mode">
                    <span className="text">
                      {groupReportPage.timeOptionText}
                    </span>
                    <i className="ladyn ladyn-qiehuan"></i>
                  </div>
                </div>
              </div>

              <div
                className={`next-btn ${
                  groupReportInput.dimension === 3 ||
                  groupReportPage.disabledNextDate
                    ? "disabled-btn"
                    : ""
                }`}
                onClick={datePlusOneDay}
              >
                <i className="ladyn ladyn-arrow-right-filling"></i>
              </div>
            </div>

            <div className="business-analysis-filter">
              {filterOptions.map((item) => (
                <span
                  key={item.type}
                  className={`filter-item ${
                    groupReportInput.data_type === item.type ? "active" : ""
                  }`}
                  onClick={() => setGroupReportInput({ data_type: item.type })}
                >
                  {item.text}
                </span>
              ))}
            </div>
          </div>

          <div className="business-analysis-content">
            <div className="shop-item-indicator">
              <div className="left-fixed">
                <div className="item indicator-title">
                  <span className="text">
                    {groupReportOutput.stat_title[0]}
                  </span>
                </div>
                {groupReportOutput.stat_data.map((item, index) => {
                  return (
                    <div className="item" key={index}>
                      {item.map((body, i) => (
                        <span className={`text ${i === 0 && "title"}`} key={i}>
                          {body.data[0]}
                        </span>
                      ))}
                    </div>
                  );
                })}
              </div>
              <div className="sub-content">
                <div className="sub-item indicator-title">
                  <div className="line-item">
                    {groupReportOutput.stat_title.slice(1).map((title, i) => (
                      <span className="item-title" key={i}>
                        {title}
                      </span>
                    ))}
                  </div>
                </div>
                {groupReportOutput.stat_data.map((item, index) => {
                  return (
                    <div className="sub-item" key={index}>
                      {item.map((body, i) => {
                        return (
                          <div className="line-item" key={i}>
                            {body.data.slice(1).map((line, j) => (
                              <span
                                className={`item ${
                                  body.control.comparison && +line
                                    ? +line > body.control.comparison_val
                                      ? "increase"
                                      : "reduction"
                                    : ""
                                }`}
                                key={j}
                              >
                                {body.control.suffix && +line
                                  ? line + body.control.suffix
                                  : line}
                              </span>
                            ))}
                          </div>
                        );
                      })}
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        </Fragment>
      )}

      <DatePicker
        title="选择时间"
        mode={groupReportPage.datePickerMode}
        value={groupReportPage.datePickerDefaultDate}
        maxDate={groupReportPage.datePickerDefaultDate}
        visible={groupReportPage.datePickerVisible}
        onDismiss={changeCustomStartPicker}
        onOk={onHandleDatePickerConfirm}
      />

      <DatePicker
        title="选择结束时间"
        mode="date"
        value={groupReportPage.datePickerEndDefaultDate}
        maxDate={groupReportPage.datePickerEndMaxDate}
        visible={groupReportPage.datePickerEndVisible}
        onDismiss={changeCustomEndPicker}
        onOk={onHandleDatePickerEndConfirm}
      />

      <Picker
        title="请选择类型"
        data={timeOptions}
        value={[groupReportInput.dimension]}
        cascade={false}
        visible={groupReportPage.timeOptionsPickerVisible}
        onDismiss={changeTimeOptionsPicker}
        onOk={onHandleTimeOptionsConfirm}
      />
    </div>
  );
};

export const groupReportRoute = {
  path: "",
  childRoutes: [{ path: "group-report", component: GroupReportPage }],
};
