interface WxConfig {
  appId: string;
  timestamp: number;
  nonceStr: string;
  signature: string;
  jsApiList: string[];
}

interface ScanQRCodeConfig {
  /** 简短的描述信息 */
  desc: string;
  /** 默认为0，扫描结果由企业微信处理，1则直接返回扫描结果 */
  needResult?: 0 | 1;
  /** 指定二维码还是一维码，["qrCode", "barCode"]，默认二者都有 */
  scanType?: string[];
}

interface ScanQRCodeResult {
  err_Info: string;
  resultStr: string;
  errMsg: string;
}

function getWxSdkConfig(config: WxConfig): Promise<any> {
  return new Promise((resolve, reject) => {
    wx.config({
      ...config,
      beta: true,
      debug: process.env.NODE_ENV === "development" ? true : false,
    });

    wx.ready(function (res: any) {
      resolve(res);
    });

    wx.error(function (err: any) {
      reject(err);
    });
  });
}

function scanQRCode(config: ScanQRCodeConfig): Promise<ScanQRCodeResult> {
  return new Promise((resolve, reject) => {
    function success(res: any) {
      resolve(res);
    }
    function error(err: any) {
      if (err.errMsg.indexOf("function_not_exist") > 0) {
        reject("版本过低请升级");
      }
      reject(JSON.stringify(err));
    }

    wx.scanQRCode({ ...config, success, error });
  });
}

export { getWxSdkConfig, scanQRCode };
