import { connectRouter } from "connected-react-router";
import { history } from "router";
import store from "store";
import { getWxSdkConfig } from "utils/wxsdk";
import { fetchWxConfigAsyncAction } from "./auth/action";
import authReducer from "./auth/reducer";
import { groupReportReducer } from "./group-report/reducer";
import homeReducer from "./home/reducer";
import { shopReportReducer } from "./shop-report/reducer";

const combineReducers = <R extends any>(reducers: R) => {
  type keys = keyof typeof reducers;
  type returnType = { [K in keys]: ReturnType<typeof reducers[K]> };
  const combinedReducer = (state: any, action: any) => {
    const newState: returnType = {} as any;
    const keys = Object.keys(reducers);
    keys.forEach((key) => {
      const result = reducers[key](state[key], action);
      newState[key as keys] = result || state[key];
    });
    return newState;
  };
  return combinedReducer;
};

const reducers = combineReducers({
  router: connectRouter(history),
  home: homeReducer,
  auth: authReducer,
  groupReport: groupReportReducer,
  shopReport: shopReportReducer,
});

export const rootReducer = (state: any, action: any) => {
  if (action.type === "@@router/LOCATION_CHANGE") {
    console.log("route_change");
    const wxConfig = state.auth.wxConfig;
    store.dispatch(fetchWxConfigAsyncAction());
    getWxSdkConfig(wxConfig);
  }

  return reducers(state, action);
};

type Skip<T, K extends keyof any> = Partial<Pick<T, Extract<keyof T, K>>> &
  Omit<T, K>;

export type RootStore = Skip<ReturnType<typeof reducers>, "router">;

export default reducers;
