import { ActionType } from "model/common";
import { GroupReportInput, GroupReportOutput } from "model/group-report";
import { useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import { RootStore } from "store/reducers";
import http from "utils/http";
import ApiUrl from "utils/url";
import {
  FETCH_GROUP_REPORT_BEGIN,
  FETCH_GROUP_REPORT_FAILURE,
  FETCH_GROUP_REPORT_SUCCESS,
  GROUP_REPORT_CHANGE_CUSTOM_END_PICKER,
  GROUP_REPORT_CHANGE_CUSTOM_START_PICKER,
  GROUP_REPORT_CHANGE_TIME_OPTIONS_PICKER,
  GROUP_REPORT_DAY_MINUS_ONE,
  GROUP_REPORT_DAY_PLUS_ONE,
  SET_GROUP_REPORT_PARAMS,
} from "./constant";

export const fetchGroupReportSuccess = (data: GroupReportOutput) => ({
  type: FETCH_GROUP_REPORT_SUCCESS,
  data,
});
export const fetchGroupReportFailure = (data: { error: any }) => ({
  type: FETCH_GROUP_REPORT_FAILURE,
  data,
});
export function fetchGroupReportAsyncAction(params: GroupReportInput) {
  return async (dispatch) => {
    dispatch({
      type: FETCH_GROUP_REPORT_BEGIN,
    });
    try {
      const { data } = await http.get(ApiUrl.GroupReport, { params });
      dispatch(fetchGroupReportSuccess(data));
    } catch (error) {
      dispatch(fetchGroupReportFailure({ error }));
    }
  };
}

export const changeDatePlusOneDayAction = () => ({
  type: GROUP_REPORT_DAY_PLUS_ONE,
});
export const changeDateMinusOneDayAction = () => ({
  type: GROUP_REPORT_DAY_MINUS_ONE,
});

export const changeTimeOptionsPickerAction = (data: { visible: boolean }) => ({
  type: GROUP_REPORT_CHANGE_TIME_OPTIONS_PICKER,
  data,
});

export const changeCustomStartPickerAction = (data: { visible: boolean }) => ({
  type: GROUP_REPORT_CHANGE_CUSTOM_START_PICKER,
  data,
});
export const changeCustomEndPickerAction = (data: { visible: boolean }) => ({
  type: GROUP_REPORT_CHANGE_CUSTOM_END_PICKER,
  data,
});

export const setGroupReportParamsAction = (data: GroupReportInput) => ({
  type: SET_GROUP_REPORT_PARAMS,
  data,
});

export const useFetchGroupReport = () => {
  const dispatch = useDispatch();

  const {
    groupReportInput,
    groupReportOutput,
    groupReportPage,
    fetchGroupReportError,
    fetchGroupReportPending,
  } = useSelector((state: RootStore) => state.groupReport);

  const fetchGroupReport = useCallback(() => {
    dispatch(fetchGroupReportAsyncAction(groupReportInput));
  }, [dispatch, groupReportInput]);

  return {
    groupReportInput,
    groupReportOutput,
    groupReportPage,
    fetchGroupReport,
    fetchGroupReportPending,
    fetchGroupReportError,
  };
};

export const useGroupReport = () => {
  const { groupReportPage, groupReportInput } = useSelector(
    (state: RootStore) => state.groupReport
  );
  const dispatch = useDispatch();
  const datePlusOneDay = useCallback(
    () => dispatch(changeDatePlusOneDayAction()),
    [dispatch]
  );
  const dateMinusOneDay = useCallback(
    () => dispatch(changeDateMinusOneDayAction()),
    [dispatch]
  );
  const changeTimeOptionsPicker = useCallback(
    () =>
      dispatch(
        changeTimeOptionsPickerAction({
          visible: !groupReportPage.timeOptionsPickerVisible,
        })
      ),
    [dispatch, groupReportPage.timeOptionsPickerVisible]
  );
  const changeCustomStartPicker = useCallback(
    () =>
      dispatch(
        changeCustomStartPickerAction({
          visible: !groupReportPage.datePickerVisible,
        })
      ),
    [dispatch, groupReportPage.datePickerVisible]
  );
  const changeCustomEndPicker = useCallback(
    () =>
      dispatch(
        changeCustomEndPickerAction({
          visible: !groupReportPage.datePickerEndVisible,
        })
      ),
    [dispatch, groupReportPage.datePickerEndVisible]
  );
  const setGroupReportInput = useCallback(
    <T extends Partial<GroupReportInput>>(params: T) =>
      dispatch(setGroupReportParamsAction({ ...groupReportInput, ...params })),
    [dispatch, groupReportInput]
  );

  return {
    datePlusOneDay,
    dateMinusOneDay,
    changeCustomStartPicker,
    changeCustomEndPicker,
    changeTimeOptionsPicker,
    setGroupReportInput,
  };
};

export type GroupReportAction = ActionType<typeof fetchGroupReportSuccess> &
  ActionType<typeof fetchGroupReportFailure> &
  ActionType<typeof changeTimeOptionsPickerAction> &
  ActionType<typeof setGroupReportParamsAction>;
