import { ActionType } from "model/common";
import { ShopReportInput, ShopReportOutput } from "model/shop-report";
import { useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import http from "utils/http";
import ApiUrl from "utils/url";
import { RootStore } from "../reducers";
import {
  FETCH_SHOP_REPORT_BEGIN,
  FETCH_SHOP_REPORT_FAILURE,
  FETCH_SHOP_REPORT_SUCCESS,
  SHOP_REPORT_CHANGE_TIME_OPTIONS_PICKER,
  SHOP_REPORT_DAY_MINUS_ONE,
  SHOP_REPORT_DAY_PLUS_ONE,
  SET_SHOP_REPORT_PARAMS,
  SHOP_REPORT_CHANGE_CUSTOM_START_PICKER,
  SHOP_REPORT_CHANGE_CUSTOM_END_PICKER,
  SHOP_REPORT_CHANGE_SHOP_OPTIONS_PICKER,
} from "./constant";

export const fetchShopReportSuccess = (data: ShopReportOutput) => ({
  type: FETCH_SHOP_REPORT_SUCCESS,
  data,
});
export const fetchShopReportFailure = (data: { error: any }) => ({
  type: FETCH_SHOP_REPORT_FAILURE,
  data,
});
export function fetchShopReportAsyncAction(params: ShopReportInput) {
  return async (dispatch) => {
    dispatch({
      type: FETCH_SHOP_REPORT_BEGIN,
    });
    try {
      const { data } = await http.get(ApiUrl.ShopReport, { params });
      dispatch(fetchShopReportSuccess(data));
    } catch (error) {
      dispatch(fetchShopReportFailure({ error }));
    }
  };
}

export const changeDatePlusOneDayAction = () => ({
  type: SHOP_REPORT_DAY_PLUS_ONE,
});
export const changeDateMinusOneDayAction = () => ({
  type: SHOP_REPORT_DAY_MINUS_ONE,
});

export const changeShopOptionsPickerAction = (data: { visible: boolean }) => ({
  type: SHOP_REPORT_CHANGE_SHOP_OPTIONS_PICKER,
  data,
});

export const changeTimeOptionsPickerAction = (data: { visible: boolean }) => ({
  type: SHOP_REPORT_CHANGE_TIME_OPTIONS_PICKER,
  data,
});

export const changeCustomStartPickerAction = (data: { visible: boolean }) => ({
  type: SHOP_REPORT_CHANGE_CUSTOM_START_PICKER,
  data,
});
export const changeCustomEndPickerAction = (data: { visible: boolean }) => ({
  type: SHOP_REPORT_CHANGE_CUSTOM_END_PICKER,
  data,
});

export const setShopReportParamsAction = (data: ShopReportInput) => ({
  type: SET_SHOP_REPORT_PARAMS,
  data,
});

export const useFetchShopReport = () => {
  const dispatch = useDispatch();

  const {
    shopReportInput,
    shopReportOutput,
    shopReportPage,
    fetchShopReportError,
    fetchShopReportPending,
  } = useSelector((state: RootStore) => state.shopReport);

  const fetchShopReport = useCallback(() => {
    dispatch(fetchShopReportAsyncAction(shopReportInput));
  }, [dispatch, shopReportInput]);

  return {
    shopReportInput,
    shopReportOutput,
    shopReportPage,
    fetchShopReport,
    fetchShopReportPending,
    fetchShopReportError,
  };
};

export const useShopReport = () => {
  const { shopReportPage, shopReportInput } = useSelector(
    (state: RootStore) => state.shopReport
  );
  const dispatch = useDispatch();
  const datePlusOneDay = useCallback(
    () => dispatch(changeDatePlusOneDayAction()),
    [dispatch]
  );
  const dateMinusOneDay = useCallback(
    () => dispatch(changeDateMinusOneDayAction()),
    [dispatch]
  );
  const changeTimeOptionsPicker = useCallback(
    () =>
      dispatch(
        changeTimeOptionsPickerAction({
          visible: !shopReportPage.timeOptionsPickerVisible,
        })
      ),
    [dispatch, shopReportPage.timeOptionsPickerVisible]
  );
  const changeShopOptionsPicker = useCallback(
    () =>
      dispatch(
        changeShopOptionsPickerAction({
          visible: !shopReportPage.shopOptionsPickerVisible,
        })
      ),
    [dispatch, shopReportPage.shopOptionsPickerVisible]
  );
  const changeCustomStartPicker = useCallback(
    () =>
      dispatch(
        changeCustomStartPickerAction({
          visible: !shopReportPage.datePickerVisible,
        })
      ),
    [dispatch, shopReportPage.datePickerVisible]
  );
  const changeCustomEndPicker = useCallback(
    () =>
      dispatch(
        changeCustomEndPickerAction({
          visible: !shopReportPage.datePickerEndVisible,
        })
      ),
    [dispatch, shopReportPage.datePickerEndVisible]
  );
  const setShopReportInput = useCallback(
    <T extends Partial<ShopReportInput>>(params: T) =>
      dispatch(setShopReportParamsAction({ ...shopReportInput, ...params })),
    [dispatch, shopReportInput]
  );

  return {
    datePlusOneDay,
    dateMinusOneDay,
    changeCustomStartPicker,
    changeCustomEndPicker,
    changeTimeOptionsPicker,
    changeShopOptionsPicker,
    setShopReportInput,
  };
};

export type ShopReportAction = ActionType<typeof fetchShopReportSuccess> &
  ActionType<typeof fetchShopReportFailure> &
  ActionType<typeof changeTimeOptionsPickerAction> &
  ActionType<typeof setShopReportParamsAction>;
