import moment from "moment";
import { ShopReportAction } from "./action";
import {
  OptionsItem,
  ShopReportInput,
  ShopReportPageState,
} from "model/shop-report";
import {
  FETCH_SHOP_REPORT_BEGIN,
  FETCH_SHOP_REPORT_FAILURE,
  FETCH_SHOP_REPORT_SUCCESS,
  SHOP_REPORT_CHANGE_TIME_OPTIONS_PICKER,
  SHOP_REPORT_DAY_MINUS_ONE,
  SHOP_REPORT_DAY_PLUS_ONE,
  SET_SHOP_REPORT_PARAMS,
  SHOP_REPORT_CHANGE_SHOP_OPTIONS_PICKER,
} from "./constant";

export function shopReportReducer(
  state: ShopReportPageState,
  action: ShopReportAction
) {
  switch (action.type) {
    case FETCH_SHOP_REPORT_BEGIN:
      return {
        ...state,
        fetchShopReportPending: true,
        fetchShopReportError: null,
      };

    case FETCH_SHOP_REPORT_SUCCESS:
      let disabledNextDate = false;
      if (state.shopReportInput.dimension === 0) {
        disabledNextDate = moment(state.shopReportInput.time_str)
          .add(1, "days")
          .isAfter(moment(), "days");
      } else if (state.shopReportInput.dimension === 1) {
        disabledNextDate = moment(state.shopReportInput.time_str)
          .add(1, "months")
          .isAfter(moment(), "months");
      } else if (state.shopReportInput.dimension === 2) {
        disabledNextDate = moment(state.shopReportInput.time_str)
          .add(1, "years")
          .isAfter(moment(), "years");
      }

      return {
        ...state,
        shopReportOutput: { ...action.data },
        shopReportPage: {
          ...state.shopReportPage,
          disabledNextDate,
          chartData: action.data.store_data[0]["data"][0],
          dataList: action.data.store_data.slice(1),
          shopList: action.data.store_options.map((item) => ({
            label: item.name,
            value: item.id,
          })) as OptionsItem[],
        },
        fetchShopReportPending: false,
        fetchShopReportError: null,
      };

    case FETCH_SHOP_REPORT_FAILURE:
      return {
        ...state,
        fetchShopReportPending: false,
        fetchShopReportError: action.data.error,
      };

    case SHOP_REPORT_DAY_PLUS_ONE: {
      let time_str = "";
      if (state.shopReportInput.dimension === 0) {
        time_str = moment(state.shopReportInput.time_str)
          .add(1, "days")
          .format("YYYY-MM-DD");
      } else if (state.shopReportInput.dimension === 1) {
        time_str = moment(state.shopReportInput.time_str)
          .add(1, "months")
          .format("YYYY-MM");
      } else if (state.shopReportInput.dimension === 2) {
        time_str = moment(state.shopReportInput.time_str)
          .add(1, "years")
          .format("YYYY");
      }

      return {
        ...state,
        shopReportInput: {
          ...state.shopReportInput,
          time_str,
        },
      };
    }

    case SHOP_REPORT_DAY_MINUS_ONE: {
      let time_str = "";
      if (state.shopReportInput.dimension === 0) {
        time_str = moment(state.shopReportInput.time_str)
          .subtract(1, "days")
          .format("YYYY-MM-DD");
      } else if (state.shopReportInput.dimension === 1) {
        time_str = moment(state.shopReportInput.time_str)
          .subtract(1, "months")
          .format("YYYY-MM");
      } else if (state.shopReportInput.dimension === 2) {
        time_str = moment(state.shopReportInput.time_str)
          .subtract(1, "years")
          .format("YYYY");
      }

      return {
        ...state,
        shopReportInput: {
          ...state.shopReportInput,
          time_str,
        },
      };
    }

    case SHOP_REPORT_CHANGE_TIME_OPTIONS_PICKER:
      return {
        ...state,
        shopReportPage: {
          ...state.shopReportPage,
          timeOptionsPickerVisible: action.data.visible,
        },
      };

    case SHOP_REPORT_CHANGE_SHOP_OPTIONS_PICKER:
      return {
        ...state,
        shopReportPage: {
          ...state.shopReportPage,
          shopOptionsPickerVisible: action.data.visible,
        },
      };

    case SET_SHOP_REPORT_PARAMS:
      let timeOptionText = "";
      let datePickerMode:
        | "datetime"
        | "date"
        | "year"
        | "month"
        | "time"
        | undefined;
      let params = {} as ShopReportInput;
      if (action.data.dimension === 0) {
        timeOptionText = "日报";
        datePickerMode = "date";
        params.time_str = moment(action.data.time_str).format("YYYY-MM-DD");
      } else if (action.data.dimension === 1) {
        timeOptionText = "月报";
        datePickerMode = "month";
        params.time_str = moment(action.data.time_str).format("YYYY-MM");
      } else if (action.data.dimension === 2) {
        timeOptionText = "年报";
        datePickerMode = "year";
        params.time_str = moment(action.data.time_str).format("YYYY");
      } else {
        timeOptionText = "自定义范围";
        datePickerMode = "date";
      }

      return {
        ...state,
        shopReportInput: {
          ...state.shopReportInput,
          ...action.data,
          ...params,
        },
        shopReportPage: {
          ...state.shopReportPage,
          timeOptionText,
          datePickerMode,
        },
      };
    default:
      return state;
  }
}
