import { Modal, Toast } from "antd-mobile";
import IconScan from "assets/scan.png";
import { useDocumentTitle } from "utils/hook";
import http from "utils/http";
import ApiUrl from "utils/url";
import { scanQRCode } from "utils/wxsdk";
import "./index.scss";

export const CheckPage = () => {
  const onClickHandle = async () => {
    const { resultStr: code } = await scanQRCode({
      desc: "扫码",
      needResult: 1,
    });

    if (code.includes("HYQ")) {
      const alert = Modal.alert("提示", "是否确认核销此券", [
        { text: "取消" },
        {
          text: "确认",
          onPress: async () => {
            try {
              Toast.loading("加载中...");
              await http.post(ApiUrl.CheckCoupon, { code });
              Toast.success("核销成功");
            } catch (error) {
              alert.close();
            }
          },
          style: "default",
        },
      ]);
    } else {
      const alert = Modal.alert("提示", "是否确认核销此券", [
        { text: "取消" },
        {
          text: "确认",
          onPress: async () => {
            try {
              Toast.loading("加载中...");
              await http.post(ApiUrl.CheckOrder, { code });
              Toast.success("核销成功");
            } catch (error) {
              alert.close();
            }
          },
          style: "default",
        },
      ]);
    }
  };

  useDocumentTitle("内部核销系统");

  return (
    <div className="check-page-container">
      <div className="scan-content" onClick={onClickHandle}>
        <img className="scan-img" src={IconScan} alt="scan" />
        <span className="scan-text">点击扫码</span>
      </div>
    </div>
  );
};

export const checkRoute = {
  path: "check",
  childRoutes: [{ path: "", component: CheckPage }],
};
